
.piece {
	height: 20px;
	width: 20px;
	border-radius: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.piece--small {
    background-color: #ccc;
    height: 15px;
    margin-right: 3px;
    width: 15px;
}

.piece--blue {
    background-color: #69a1b0;
}

.piece--red {
    background-color: #fe5a59
}

.piece--master:before {
    content: " ";
    position: absolute;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: #666;
}

.piece--blue.piece--master:before {background-color: #296075;}
.piece--red.piece--master:before {background-color: #cd2735;}



