.slot {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 50px;
	height: 50px;
	border-bottom: 1px solid #000;
}

.slot:last-child {
	border: 0;
}

.active {
    color: white;
    background-color: #ccc;
}

.candidate {
    background-color: #ccc;
}



